import React from 'react'
import { formatDistance } from 'date-fns'
import { sv } from 'date-fns/locale'

const formatDate = date =>
  date.toLocaleDateString('sv-SE', {
    month: 'short',
    day: 'numeric',
    hour: 'numeric',
    minute: 'numeric',
  })

const ViewBooking = ({ booking }) => {
  const {
    item: { name: item },
    user: { pretty_name: user },
    start,
    end,
    description,
    confirmed,
  } = booking

  const bookingInfoText = booking.restricted_timeslot
    ? `Begrnsad tidsperiod fr bokningar av ${item} utfrdad av ${user}.
    Under en begrnsad tidsperiod mste alla bokningar godknnas manuellt.`
    : `Bokning av ${item} fr ${user}.`
  return (
    <>
      <p>{bookingInfoText}</p>
      <p>
        {confirmed
          ? 'Bokningen r bekrftad.'
          : 'Bokningen r inte bekrftad nnu.'}
      </p>
      <p>
        {`${formatDate(start)} - ${formatDate(end)}`}
        {` (${formatDistance(end, start, { locale: sv })})`}
      </p>
      <p>{description}</p>
    </>
  )
}

export default ViewBooking
