const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "mindre n en sekund",
    other: "mindre n {{count}} sekunder",
  },

  xSeconds: {
    one: "en sekund",
    other: "{{count}} sekunder",
  },

  halfAMinute: "en halv minut",

  lessThanXMinutes: {
    one: "mindre n en minut",
    other: "mindre n {{count}} minuter",
  },

  xMinutes: {
    one: "en minut",
    other: "{{count}} minuter",
  },

  aboutXHours: {
    one: "ungefr en timme",
    other: "ungefr {{count}} timmar",
  },

  xHours: {
    one: "en timme",
    other: "{{count}} timmar",
  },

  xDays: {
    one: "en dag",
    other: "{{count}} dagar",
  },

  aboutXWeeks: {
    one: "ungefr en vecka",
    other: "ungefr {{count}} veckor",
  },

  xWeeks: {
    one: "en vecka",
    other: "{{count}} veckor",
  },

  aboutXMonths: {
    one: "ungefr en mnad",
    other: "ungefr {{count}} mnader",
  },

  xMonths: {
    one: "en mnad",
    other: "{{count}} mnader",
  },

  aboutXYears: {
    one: "ungefr ett r",
    other: "ungefr {{count}} r",
  },

  xYears: {
    one: "ett r",
    other: "{{count}} r",
  },

  overXYears: {
    one: "ver ett r",
    other: "ver {{count}} r",
  },

  almostXYears: {
    one: "nstan ett r",
    other: "nstan {{count}} r",
  },
};

const wordMapping = [
  "noll",
  "en",
  "tv",
  "tre",
  "fyra",
  "fem",
  "sex",
  "sju",
  "tta",
  "nio",
  "tio",
  "elva",
  "tolv",
];

export const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace(
      "{{count}}",
      count < 13 ? wordMapping[count] : String(count),
    );
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return "om " + result;
    } else {
      return result + " sedan";
    }
  }

  return result;
};
